@import 'button.sass';
@import 'fonts.sass';
@import 'card.sass';


$primary: #10b981;
$primary-bg: #f5f5f5;
$textprimarycolor: #2b2626;
$primary-products: #845ec2;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  background-color: none; }

.body {
  width: 100%;

  background-color: $primary-bg; }

.header {
  min-height: 888px;
  margin-top: 56px; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.controls {
  display: flex;
  justify-content: end;
  gap: 25px;
  padding: 0 85px; }

.logo__title {
  display: inline-block;
  color: $primary;
  font-size: 40px;
  line-height: 49px;
  font-weight: 400;
  font-style: italic;
  margin-right: auto; }

.logo__title > span {
  color: black; }

.search__input__label {
  position: relative; }

.search__input__btn {
  position: absolute;
  top: 11px;
  right: 18px;
  width: 38px;
  height: 38px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  background-color: $primary; }

.search__input__btn > img {
  background-color: transparent; }


.search__input {
  width: 322px;
  height: 63px;
  box-shadow: rgba(46, 131, 158, 0.07);
  border: none;
  border-radius: 10px;
  padding: 0 18px 0 32px;
  background-color: #fff; }

.basket__text {
  display: inline-block;
  margin-left: 15px; }


.login > img {
  background-color: transparent; }


.login__text {
  margin-left: 15px;
  display: inline-block;
  background-color: transparent; }

.links {
  height: 81px;
  margin-top: 63px;
  background-color: $primary;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 75px; }


.link {
  display: inline;
  background-color: transparent; }

.link > a {
  background-color: transparent;
  color: #fff;
  text-decoration: none; }

/* About */
.about {
  margin-left: 85px; }


.about__descr {
  width: 600px;
  display: inline-flex;
  flex-direction: column;
  margin-top: 126px;
  margin-bottom: 85px; }

.title {
  font-weight: 600;
  font-size: 64px;
  line-height: 78px;
  color: $textprimarycolor; }

.about__descr_text {
  font-weight: 500;
  font-size: 21px;
  line-height: 35px;
  color: $textprimarycolor;
  margin-top: 30px; }

.about__descr__container {
  display: flex;
  flex-direction: column;
  gap: 110px;
  margin-top: 52px; }

.about__descr_btn {
  width: 263px;
  height: 70px;
  background-color: $primary;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  font-size: 21px;
  line-height: 26px;
  color: #fff;
  cursor: pointer;
  box-shadow: rgba(132, 94, 194, 0.15); }


.icons {
  position: relative;
  width: 180px; }

.icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid #fff;
  z-index: 2; }

.icon_2 {
  position: absolute;
  left: 37px; }

.icon_3 {
  position: absolute;
  left: 74px; }

.icon_4 {
  position: absolute;
  left: 112px; }

.about__reviews {
  display: flex;
  justify-content: start;
  align-items: center; }

.about__reviews_descr {
  font-size: 26px;
  line-height: 35px;
  font-weight: 500;
  color: $textprimarycolor;
  display: inline-block;
  width: 120px; }

.about__interactive {
  width: 600px;
  display: inline-block;
  position: relative;
  left: 114px;
  top: -30px; }

.play {
  width: 115px;
  display: inline-block;
  position: relative;
  left: -47px;
  top: -8px; }

.play__img {
  display: block;
  width: 69px;
  height: 69px;
  border-radius: 100%;
  overflow: hidden;
  margin: 0 auto; }


.play__img img {
  width: 112px;
  height: 75px;
  margin: 0 0 0 -23px; }


.play__text {
  color: $textprimarycolor;
  font-size: 21px;
  line-height: 35px;
  font-weight: 500;
  border: none;
  display: block;
  margin: 18px auto; }


.about__interactive__title {
  display: inline-block;
  position: absolute;
  width: 500px;
  height: 233px;
  left: 141px;
  top: 0;
  z-index: 2;
  background-color: transparent; }

.title__word {
  display: inline-block;
  background-color: transparent;
  color: $textprimarycolor;
  font-size: 40px;
  line-height: 49px;
  position: relative;
  font-weight: 600;
  /* left: -58px */
  top: 110px; }

.title__word_R {
  left: -59px;
  top: 110px;
  transform: rotate(-93deg); }

.title__word_e {
  left: -90px;
  top: 82px;
  transform: rotate(-93deg); }

.title__word_d {
  left: -116px;
  top: 57px;
  transform: rotate(-76deg); }

.title__word_B {
  left: -135px;
  top: 20px;
  transform: rotate(-61deg); }

.title__word_e-e {
  left: -152px;
  top: -3px;
  transform: rotate(-54deg); }

.title__word_a {
  left: -164px;
  top: -22px;
  transform: rotate(-45deg); }

.title__word_t {
  left: -172px;
  top: -37px;
  transform: rotate(-40deg); }

.title__word_s {
  left: -180px;
  top: -46px;
  transform: rotate(-36deg); }

.title__word_M {
  left: -176px;
  top: -63px;
  transform: rotate(-17deg); }

.title__word_I {
  left: -181px;
  top: -69px;
  transform: rotate(-8deg); }

.title__word_9 {
  left: -185px;
  top: -69px;
  transform: rotate(-8deg); }

.title__word_c {
  left: -189px;
  top: -67px;
  transform: rotate(-3deg); }

.title__word_2 {
  left: -192px;
  top: -63px;
  transform: rotate(18deg); }

.title__word_z {
  left: -196px;
  top: -56px;
  transform: rotate(25deg); }

.title__word_m {
  left: -204px;
  top: -41px;
  transform: rotate(33deg); }

.title__word_sl {
  left: -214px;
  top: -23px;
  transform: rotate(42deg); }

.title__word_a-a {
  left: -225px;
