.headphones__backgr {
  display: inline-block;
  background-color: #ecfef4;
  width: 272px;
  height: 477px;
  border-radius: 136px; }

.headphones {
  width: 332px;
  position: absolute;
  display: inline-block;
  background-color: transparent;
  left: 100px; }

.headphones__img {
  height: 299px;
  background-color: transparent;
  display: block;
  margin: 0 auto;
  margin-top: 76px; }

.headphones__text {
  background-color: transparent;
  display: block;
  margin-top: 26px;
  text-align: center;
  color: $textprimarycolor;
  font-size: 31px;
  line-height: 38px; }

.star__first {
  position: absolute;
  top: -94px;
  left: 51px; }

.star__second {
  position: absolute;
  top: 460px;
  left: 429px; }

/* products */
.products {
  background-color: $primary-products;
  padding-top: 73px;
  padding-left: 85px;
  height: 591px; }

.images {
  display: inline-block; }

.images__title {
  font-size: 60px;
  line-height: 73px;
  text-shadow: 0px 0px 2px white;
  font-weight: 900;
  color: $primary-products; }

.images__container {}

.images__container_products {
  padding-top: 37px;
  position: relative; }

.images__container > img {
  width: 190px;
  height: 331px; }

.images__container_1 {}

.images__container_2 {
  position: relative;
  top: -69px;
  margin: 0 33px; }

.images__container_3 {}

.price {
  font-size: 31px;
  line-height: 35px;
  font-weight: 400;
  color: #ffffff; }

.price_products {
  margin-left: 201px; }

.price__text {
  display: inline-block; }

.price__number {
  display: inline-block;
  font-weight: 600;
  margin-left: 20px; }

.product-descr {
  display: inline-block; }

.product-descr-normal {
  position: relative;
  width: 560px;
  top: -185px;
  left: 55px; }

.product-descr__title {
  font-size: 44px;
  line-height: 54px;
  color: #fff;
  font-weight: 600; }

.product-descr__description {
  margin-top: 10px;
  font-size: 21px;
  line-height: 35px;
  color: #fff;
  font-weight: 500; }

/* sellers */
.sellers {
  padding-top: 130px;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.sellers__title {
  font-weight: 600;
  line-height: 54px;
  font-size: 44px;
  text-align: center;
  color: $textprimarycolor; }

.sellers__title-margin-bt {
  margin-bottom: 55px; }

.tab {
  display: flex;
  flex-direction: column;
  align-items: center; }

.tab__btn-wrapper {
  margin-bottom: 99px; }

.tab__card-wrapper {
  display: flex;
  gap: 93px;
  flex-wrap: wrap; }

/* earphones */
.earphones {
  padding-top: 130px;
  display: flex;
  flex-direction: column;
  justify-content: center; }


/* launches */
.launches {
  padding-top: 130px; }

.launches__title {
  font-weight: 600;
  line-height: 54px;
  font-size: 44px;
  text-align: center;
  color: $textprimarycolor; }

.launches__title-margin-bt {
  margin-bottom: 120px; }

.launches__wrapper {
  height: 591px;
  padding: 62px 85px 0px;
  display: flex;
  justify-content: space-around; }

.bg-primary-products {
  background-color: $primary-products; }

.launches-descr {
  display: inline-flex;
  flex-direction: column; }

.launches__images {
  display: flex;
  gap: 45px; }

.launches__images > img {
  width: 237px;
  height: 417px; }

.launches__images > .down {
  margin-top: 50px; }

.launches-descr-normal {
  position: relative;
  width: 584px; }

.launches-descr__title {
  font-size: 44px;
  line-height: 54px;
  color: #fff;
  font-weight: 600; }

.launches-descr__description {
  margin-top: 29px;
  font-size: 21px;
  line-height: 35px;
  color: #fff;
  font-weight: 500; }

.price_launchess {
  margin-top: 26px; }

.launches__colors {
  margin-top: 50px;
  display: flex;
  gap: 31px; }

.color {
  width: 43px;
  height: 43px;
  border-radius: 50%; }

.color.black {
  background-color: #111111; }

.color.yellow {
  background-color: #fff500; }

.color.blue {
  background-color: #4484ff; }

.launches__button-container {
  display: flex;
  margin-top: 50px;
  gap: 77px; }

@media (max-width: 1140px) {
  .search__input {
    width: 218px; }

  .about__descr {
    width: 550px; } }


@media (max-width: 968px) {
  .about {
    margin-left: 45px; }

  .basket {
    display: none; }

  .login {
    display: none; }

  .about__descr__container {
    flex-direction: row;
    gap: 50px; }

  .about__descr {
    width: 475px; } }


@media (max-width: 568px) {
  .controls {
    flex-direction: column;
    padding: 0 30px; }

  .search__input {
    width: 100%; }

  .links {
    gap: 40px; }

  .about {
    margin-left: 20px; }

  .title {
    font-size: 45px;
    line-height: 50px; }

  .about__descr {
    width: 330px; }

  .about__descr__container {
    flex-direction: column-reverse; }

  .about__descr_btn {
    width: 100%; } }


@media (max-width: 360px) {
  .links {
    gap: 25px; } }
